import React, { useState } from 'react'

import bg01 from '../utils/img/bg01.png'
import bg02 from '../utils/img/bg02.png'
import bg03 from '../utils/img/bg03.png'

const Landing = () => {
  const [slider, setSlider] = useState({
    img: bg01,
    bgColor: `#000F26`,
    val: 1
  });

  const setFirstSlider = () => {
    setSlider({
      img: bg01,
      bgColor: `#000F26`,
      val: 1
    });
  }

  const setSecondSlider = () => {
    setSlider({
      img: bg02,
      bgColor: `#212121`,
      val: 2
    });
  }

  const setThirdSlider = () => {
    setSlider({
      img: bg03,
      bgColor: `#00252B`,
      val: 3
    });
  }

  return (
    <section className={`min-h-screen mt-20 text-white bg-right bg-no-repeat`} style={{ backgroundImage: `url(${slider.img})`, backgroundColor: slider.bgColor }}>
      <div className="container-center px-5 py-24">
        <div className={`w-100 md:w-1/2 ${(slider.val === 1) ? '' : 'hidden'}`}>
          <h1 className='mb-8 font-bold'>Building powerful digital solutions for a better world</h1>
          <p className='mb-6 text-xl'>Harness the power of technology to set yourself apart and reshape the way your business finds success.</p>
          <button className='btn bg-white text-slate-800 py-3 px-8 text-lg'>Tell us about your project &rarr;</button>
        </div>
        <div className={`w-100 md:w-1/2 ${(slider.val === 2) ? '' : 'hidden'}`}>
          <h1 className='mb-8 font-bold'>Helping educate over 20 million learners around the world</h1>
          <p className='mb-6 text-xl'>As the official tech partner for edX since 2012, we have helped learners access education on-the-go with thousands of courses at their disposal, anywhere anytime.</p>
          <div className='flex flex-wrap'>
            <button className='btn bg-blue-500 text-white py-3 px-6 text-lg mt-2 mx-3'>View our success story &rarr;</button>
            <button className='btn bg-white text-slate-800 py-3 px-6 text-lg mt-2'>Tell us about your project &rarr;</button>
          </div>
        </div>
        <div className={`w-100 md:w-1/2 ${(slider.val === 3) ? '' : 'hidden'}`} >
          <h1 className='mb-8 font-bold'>Helping build one of the worlds largest travel search engines</h1>
          <p className='mb-6 text-xl'>We have partnered with KAYAK to revolutionize the traveling experience for over 50 million happy users around the globe and continue to build an industry-leading legacy.</p>
          <div className='flex flex-wrap'>
            <button className='btn bg-blue-500 text-white py-3 px-6 text-lg mt-2 mx-3'>View our success story &rarr;</button>
            <button className='btn bg-white text-slate-800 py-3 px-6 text-lg mt-2'>Tell us about your project &rarr;</button>
          </div>
        </div>
        <div className='flex space-x-2 mt-24'>
          <input type="radio" name='slider' onClick={setFirstSlider} />
          <input type="radio" name='slider' onClick={setSecondSlider} />
          <input type="radio" name='slider' onClick={setThirdSlider} />
        </div>
      </div>
    </section>
  )
}

export default Landing