import React from 'react'

import DataImg from '../utils/img/DataAI.png'
import EnterpriseImg from '../utils/img/EnterpriseSoftware.png'
import MobileImg from '../utils/img/MobileApp.png'
import WebImg from '../utils/img/WebApp.png'

const Services = () => {
    return (
        <div>
            <div className='flex flex-col md:flex-row flex-wrap text-[#223554]'>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='w-20 h-20' src={DataImg} alt="Services" />
                    <h2 className='font-bold mb-6'>Data and AI</h2>
                    <p className='sm:text-xl mb-8'>We can help you unlock your business potential with our cutting-edge Machine Learning and Artificial Intelligence capabilities.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Learn More</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='w-20 h-20' src={WebImg} alt="Services" />
                    <h2 className='font-bold mb-6'>Web Apps</h2>
                    <p className='sm:text-xl mb-8'>We provide custom-tailored solutions for each phase of development from designing to coding and implementation.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Learn More</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='w-20 h-20' src={MobileImg} alt="Services" />
                    <h2 className='font-bold mb-6'>Mobile Apps</h2>
                    <p className='sm:text-xl mb-8'>We build native apps optimized for iOS and Android to provide users with a seamless mobile experience that leverages the full power of the OS.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Learn More</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='w-20 h-20' src={EnterpriseImg} alt="Services" />
                    <h2 className='font-bold mb-6'>Enterprise software</h2>
                    <p className='sm:text-xl mb-8'>We bring your ideas to life with our turnkey solutions that are custom-designed to fit into your enterprise software ecosystem.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Learn More</button>
                </div>
            </div>
        </div>
    )
}

export default Services