import React, { useState } from 'react'
import { useSelector } from 'react-redux'

const Navbar = () => {

    const [navBarToggle, setNavBarToggle] = useState('-left-full');
    const { APP_LOGO_SVG } = useSelector((state) => state.AppDetail);

    // Toggle nav links on small screens
    const toggleNavBar = () => {
        setNavBarToggle(((navBarToggle === 'left-0') ? '-left-full' : 'left-0'));
    }

    return (
        <header className='py-5 fixed top-0 left-0 w-screen bg-white z-50'>
            <div className='container-center px-5 flex justify-between'>
                <div><a href="/">{APP_LOGO_SVG}</a></div>
                <div className='lg:w-full ml-4 xl:ml-10'>
                    <button onClick={toggleNavBar} className='lg:hidden text-[#85BBED] text-2xl h-[38px]'><i className="fa-solid fa-bars"></i></button>
                    <ul className={`p-5 lg:p-0 flex flex-col lg:flex-row w-full border-t bg-white border-[#d0d0d0] lg:border-0 fixed lg:static top-[78px] ${navBarToggle}`}>
                        <div className='lg:flex items-center justify-between lg:w-full lg:mr-4'>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Why Arbisoft</span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>What we do<i className="fa-sharp fa-solid fa-angle-right ml-2 lg:text-[#85BBED] lg:rotate-90"></i></span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Who we work with<i className="fa-sharp fa-solid fa-angle-right ml-2 lg:text-[#85BBED] lg:rotate-90"></i></span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Our Products<i className="fa-sharp fa-solid fa-angle-right ml-2 lg:text-[#85BBED] lg:rotate-90"></i></span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Our Work</span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Inside Arbisoft<i className="fa-sharp fa-solid fa-angle-right ml-2 lg:text-[#85BBED] lg:rotate-90"></i></span></li>
                            <li className='py-[10px] lg:py-0'><span className='cursor-pointer font-thin'>Careers<i className="fa-sharp fa-solid fa-angle-right ml-2 lg:text-[#85BBED] lg:rotate-90"></i></span></li>
                        </div>
                        <button className='btn py-3 lg:py-2 px-8 lg:px-[30px] text-[18px] lg:text-base bg-[#0a76db] text-white w-fit lg:min-w-max cursor-pointer'>Contact us</button>
                    </ul>
                </div>
            </div>
        </header>
    )
}

export default Navbar