import React, { useState } from 'react'
import Services from './Services';
import Product from './Product';
import Industries from './Industries';

const LinkContent = () => {
    const [content, setContent] = useState({
      component: <Industries />,
      name: 'Industries'
    });
  return (
    <section className='my-24'>
        <div className='px-5 container-center flex space-x-1 md:space-x-5 text-slate-700'>
            <button onClick={()=>{setContent({component: <Industries />, name: 'Industries'})}} className={`border text-sm py-2 md:py-3 px-5 md:px-9 md:text-[17px] font-bold border-slate-400 ${(content.name === 'Industries') ? 'text-white bg-purple-900':''}`}>Industries</button>
            <button onClick={()=>{setContent({component: <Services />, name: 'Services'})}} className={`border text-sm py-2 md:py-3 px-5 md:px-9 md:text-[17px] font-bold border-slate-400 ${(content.name === 'Services') ? 'text-white bg-purple-900':''}`}>Services</button>
            <button onClick={()=>{setContent({component: <Product />, name: 'Product'})}} className={`border text-sm py-2 md:py-3 px-5 md:px-9 md:text-[17px] font-bold border-slate-400 ${(content.name === 'Product') ? 'text-white bg-purple-900':''}`}>Product</button>
            <button className='text-blue-500 hidden md:block'>All Industries &rarr;</button>
        </div>
        <div className='px-5 container-center'>{content.component}</div>
    </section>
  )
}

export default LinkContent