import React, { useState } from 'react'
import ClientCard from './ClientCard';

import PoulImg from '../utils/img/paul-client.png'
import kayakImg from '../utils/img/kayak.png'

import AliceImg from '../utils/img/alice.png'
import WorldBankImg from '../utils/img/worldbank.png'

const ClientPromo = () => {
  const [clientCrousal, setClientCrousal] = useState(1);
  const [crousalMove, setCrousalMove] = useState('0px');

  const changeCrousal = async (number) => {
    if (number >= 1 && number <= 6) {
      await setClientCrousal(number);
      await crousalChangeHandler();
    }
  }

  const crousalChangeHandler = () => {
    setCrousalMove(`${(clientCrousal-1)*560}px`)
  }

  return (
    <section className='py-24 bg-[#223554] text-white'>
      <div className="container-center px-5">
        <h2 className='mb-[70px] text-center font-bold'>What our clients say</h2>
        <div>
          <div className='overflow-hidden'>
            <div className='flex relative' style={{right: crousalMove}}>
              <ClientCard clientImg={PoulImg} clientName='Paul1 English' clientPost='Co-Founder KAYAK' companyImg={kayakImg} description='I have managed remote teams now for over ten years, and our early work with Arbisoft is the best experience I have had for off-site contractors.' />
              <ClientCard clientImg={AliceImg} clientName='Alice Danon' clientPost='Project Coordinator, World Bank' companyImg={WorldBankImg} description='They delivered a high-quality product and their customer service was excellent. Weve had other teams approach us, asking to use it for their own projects.' />
            </div>
          </div>
          <div className='mt-8 flex justify-between items-center text-[#9fb1f4]'>
            <button onClick={() => { changeCrousal(clientCrousal - 1) }} className='btn border border-[#9fb1f4] w-9 h-9 lg:w-16 lg:h-16 flex items-center justify-center text-2xl'><i className="fa-solid fa-arrow-left"></i></button>
            <span className='hidden md:block space-x-3'>
              <button className={`btn p-[5px] ${(clientCrousal === 1) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(1) }}></button>
              <button className={`btn p-[5px] ${(clientCrousal === 2) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(2) }}></button>
              <button className={`btn p-[5px] ${(clientCrousal === 3) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(3) }}></button>
              <button className={`btn p-[5px] ${(clientCrousal === 4) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(4) }}></button>
              <button className={`btn p-[5px] ${(clientCrousal === 5) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(5) }}></button>
              <button className={`btn p-[5px] ${(clientCrousal === 6) ? 'bg-slate-100' : 'bg-slate-500'}`} onClick={() => { changeCrousal(6) }}></button>
            </span>
            <span className='hidden'>
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 1) ? true : false} />
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 2) ? true : false} />
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 3) ? true : false} />
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 4) ? true : false} />
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 5) ? true : false} />
              <input type="radio" name="client-crousal" onChange={crousalChangeHandler} checked={(clientCrousal === 6) ? true : false} />
            </span>
            <button onClick={() => { changeCrousal(clientCrousal + 1) }} className='btn border border-[#9fb1f4] w-9 h-9 lg:w-16 lg:h-16 flex items-center justify-center text-2xl'><i className="fa-solid fa-arrow-right"></i></button>
          </div>
        </div>
      </div>
    </section>
  )
}

export default ClientPromo