import React from 'react'

const BecomePartnerCard = (props) => {
  let  { direction, color, heading, btnColor, btnText, img } = props;
  return (
    <div className={`w-full md:w-1/2`} style={{backgroundColor: color}}>
        <div className={`pt-8 pr-12 flex items-end pb-16 pl-8 md:max-w-[550px] ${(direction === 'left')? 'mr-auto':'ml-auto'}`}>
            <div className='w-4/5'>
                <h3 className='text-[28px] lg:text-[32px] text-[#223554] font-bold mb-7'>{heading}</h3>
                <button className='btn text-white py-[10px] px-5' style={{backgroundColor: btnColor}}>{btnText}</button>
            </div>
            <div className='w-1/5'>
                <img className='relative top-12 w-28' src={img} alt="become-partner" />
            </div>
        </div>
    </div>
  )
}

export default BecomePartnerCard