import React from 'react'
import BecomePartnerCard from './BecomePartnerCard'

import bulbImg from '../utils/img/bulb-partner.png'
import flagImg from '../utils/img/flag-partner.svg'

const BecomePartnerPromo = () => {
  return (
    <div className='my-36 flex flex-col space-y-8 md:space-y-0 md:flex-row'>
        <BecomePartnerCard direction='right' color='#EFFAFF' heading='Become a part of our global team!' btnColor='#223554' btnText='Apply here' img={flagImg} />
        <BecomePartnerCard direction='left' color='#FCEED8' heading='Partner with us for your next venture!' btnColor='#F37224' btnText="Let's get started" img={bulbImg} />
    </div>
  )
}

export default BecomePartnerPromo