import React from 'react'
import footerImg from '../utils/img/footer-img.png'
import { useSelector } from 'react-redux'

const Footer = () => {
    const {ARBISOFT_FB_LINK, ARBISOFT_INSTAGRAM_LINK, ARBISOFT_LINKEDIN_LINK, ARBISOFT_TWITTER_LINK} = useSelector(state => state.AppDetail);
    return (
        <footer className='py-10 bg-[#0D3F8A] text-gray-200'>
            <section className='container-center px-5'>
                <div className='flex justify-between flex-wrap mb-5'>
                    <div className='text-[14px] lg:text-[18px]'>
                        <p className='leading-5 my-4'>For job opportunities, reach out to <a className='text-white' href="mailto:pod@arbisoft.com">pod@arbisoft.com</a></p>
                        <p className='leading-5 my-4'>For business enquiries, reach out to <a className='text-white' href="mailto:contact@arbisoft.com">contact@arbisoft.com</a></p>
                    </div>
                    <div>
                        <img src={footerImg} className='w-[206px]' alt="mail-envolpe" />
                    </div>
                </div>
                <div className='flex flex-col md:flex-row text-sm mb-8'>
                    <div className='w-full mb-8 md:mb-0 md:mr-5 lg:mr-12'>
                        <span className='block'>+1 (469) 215 2958</span>
                        <address className='block not-italic'>2035 Central Cir Suite #201 McKinney, TX 75069</address>
                    </div>
                    <div className='w-full mb-8 md:mb-0 md:mr-5 lg:mr-12'>
                        <span className='block'>+49 157 39369191</span>
                        <address className='block not-italic'>KAYAK Technology Center, Strae der Pariser Kommune 8, 10243 Berlin.</address>
                    </div>
                    <div className='w-full mb-8 md:mb-0 md:mr-5 lg:mr-12'>
                        <span className='block'>(042) 37498533</span>
                        <address className='block not-italic'>25 Canal Rd, Westwood Colony Lahore, Punjab 54000</address>
                    </div>
                    <div className='w-full mb-8 md:mb-0 md:mr-5 lg:mr-12'>
                        <address className='block not-italic'>10-D (West), 1st Floor Taimur Chambers Fazl-ul-Haq Road, Blue Area Islamabad 44010</address>
                    </div>
                </div>
            </section>
            <section className='mt-8 border-t border-white container-center px-5 pt-8 flex flex-col lg:flex-row lg:items-center lg:justify-between'>
                <div className='mb-4 lg:mb-0'>
                    <span className='block md:inline mb-2 md:mb-0 mr-3 lg:mr-8 xl:mr-16'> 2023 Arbisoft. All Rights Reserved.</span>
                    <span className='block md:inline mb-2 md:mb-0 mr-3 lg:mr-8 xl:mr-16'><a href="/">Privacy Policy</a></span>
                    <span className='block md:inline mb-2 md:mb-0 mr-3 lg:mr-8 xl:mr-16'><a href="/">Security Policy</a></span>
                    <span className='block md:inline mb-2 md:mb-0 mr-3 lg:mr-8 xl:mr-16'><a href="/">Imprint</a></span>
                </div>
                <div className='flex items-center space-x-4'>
                    <p>Find us on:</p>
                    <a href={ARBISOFT_FB_LINK} rel='noreferrer' target='_blank' className='btn bg-[#0a76db] h-10 w-10 flex items-center justify-center'><i className="fa-brands fa-facebook-f"></i></a>
                    <a href={ARBISOFT_INSTAGRAM_LINK} rel='noreferrer' target='_blank' className='btn bg-[#0a76db] h-10 w-10 flex items-center justify-center'><i className="fa-brands fa-instagram"></i></a>
                    <a href={ARBISOFT_LINKEDIN_LINK} rel='noreferrer' target='_blank' className='btn bg-[#0a76db] h-10 w-10 flex items-center justify-center'><i className="fa-brands fa-linkedin"></i></a>
                    <a href={ARBISOFT_TWITTER_LINK} rel='noreferrer' target='_blank' className='btn bg-[#0a76db] h-10 w-10 flex items-center justify-center'><i className="fa-brands fa-twitter"></i></a>
                </div>
            </section>
        </footer>
    )
}

export default Footer