import React from 'react'

import EdlyImg from '../utils/img/Edly.png'
import MoonbeamImg from '../utils/img/moonbeam.png'
import HirestreamImg from '../utils/img/hirestream.png'
import IlmxImg from '../utils/img/Ilmx.png'

const Product = () => {
    return (
        <div>
            <div className='flex flex-col md:flex-row flex-wrap text-[#223554]'>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='h-16 mb-8' src={EdlyImg} alt="Services" />
                    <p className='sm:text-xl mb-8'>A learning and training management system that responds to your needs and delivers accordingly while simultaneously connecting you to your audience.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Explore Edly</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='h-16 mb-8' src={MoonbeamImg} alt="Services" />
                    <p className='sm:text-xl mb-8'>A podcast player that enables listeners to sift through short audio clips from different podcasts and find new shows that they otherwise wouldnt come across.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Explore Edly</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='h-16 mb-8' src={HirestreamImg} alt="Services" />
                    <p className='sm:text-xl mb-8'>An applicant tracking system and recruiting software that gives you more power over hiring decisions that matter. Hirestream automates the tasks that slow you down and refines your process through single-click bulk applicant management.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Explore Edly</button>
                </div>
                <div className='w-full md:w-1/2 pt-16 pb-10 px-8 bg-cover border'>
                    <img className='h-16 mb-8' src={IlmxImg} alt="Services" />
                    <p className='sm:text-xl mb-8'>A social enterprise committed to connecting and empowering learners by making e-learning opportunities accessible and affordable for all.</p>
                    <button className='btn bg-white px-6 border border-slate-700 py-2 text-slate-800'>Explore Edly</button>
                </div>
            </div>
        </div>
    )
}

export default Product