import React from 'react'

import peopleImg from '../utils/img/people.png'

const OurPeoples = () => {
    return (
        <section className='py-24 text-[#223554]'>
            <div className='flex flex-col md:flex-row md:items-center md:justify-end container-center'>
                <img src={peopleImg} className='w-full md:w-1/3 md:relative md:left-20' alt="OurPeople" />
                <div className='w-full md:w-3/5 p-12 bg-[#F4F5F7]'>
                    <div className='w-full md:w-4/5 ml-auto'>
                        <h2 className='font-bold mb-4'>We are our  people</h2>
                        <p className='mb-8 text-[18px]'>Our team of over 800 blends the skills of engineers, developers, UX architects, and designers to craft impactful solutions. Together, we have aided businesses in their journey toward digital transformation by delivering custom software that drives change.</p>
                        <button className='btn bg-[#0a76db] text-white px-8 py-3'>Learn More</button>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default OurPeoples