import React from 'react'
import peopleIcon from '../utils/img/icon-people.svg'
import homeIcon from '../utils/img/icon-home.svg'
import bulbIcon from '../utils/img/icon-bulb.svg'
import clutchIcon from '../utils/img/icon-clutch.svg'

const OurJourney = () => {
  return (
    <section className='text-[#223554] mt-32 container-center px-5'>
        <h1 className='text-center my-12 font-bold'>A Journey of <span className='text-[#0A76BD]'>15 Years</span></h1>
        <div className='flex flex-col border border-t-0 md:flex-row mt-4 mb-8 p-12 rounded-xl'>
            <div className='w-full md:w-1/4 text-center md:border-r p-4'>
                <img className='block mx-auto mb-6' src={peopleIcon} alt="JourneyIcons" />
                <h3 className='text-[45px] font-bold'>800 +</h3>
                <p>People onboard</p>
            </div>
            <div className='w-full md:w-1/4 text-center md:border-r p-4'>
                <img className='block mx-auto mb-6' src={homeIcon} alt="JourneyIcons" />
                <h3 className='text-[45px] font-bold'>4</h3>
                <p>Global Offices</p>
            </div>
            <div className='w-full md:w-1/4 text-center md:border-r p-4'>
                <img className='block mx-auto mb-6' src={bulbIcon} alt="JourneyIcons" />
                <h3 className='text-[45px] font-bold'>550 +</h3>
                <p>Project Completed</p>
            </div>
            <div className='w-full md:w-1/4 text-center p-4'>
                <img className='block mx-auto mb-6' src={clutchIcon} alt="JourneyIcons" />
                <h3 className='text-[45px] font-bold'>4.9</h3>
                <p>Clutch Rating</p>
            </div>
        </div>
    </section>
  )
}

export default OurJourney