import React from 'react'

const ClientCard = (props) => {
    let { clientImg, clientName, clientPost, description, companyImg } = props;
  return (
    <div className='px-5 lg:px-6 py-7 w-full min-w-full md:min-w-[45%] md:w-[45%] md:mx-5 lg:mx-8 bg-white text-[#223554]'>
        <div className='mb-10 flex'>
            <div>
                <img src={clientImg} className='h-[56px] w-[56px] lg:h-24 lg:w-24 rounded-full' alt="client-Img" />
            </div>
            <div className='ml-5'>
                <h3 className='text-[24px] lg:text-[28px] font-bold'>{clientName}</h3>
                <p className='text-xs md:text-base'>{clientPost}</p>
            </div>
        </div>
        <div className='md:h-40 mb-10'>
            <p className='md:text-[18px]'>{description}</p>
        </div>
        <div className='border-t-2 border-gray-400'>
            <img src={companyImg} className='block mx-auto py-9 max-h-36' alt="company" />
        </div>
    </div>
  )
}

export default ClientCard