import React from 'react'
import MGP from '../utils/img/ms-goldpartner.svg'
import MC from '../utils/img/mongoob.svg'
import ZYTE from '../utils/img/zyte.svg'
import EDX from '../utils/img/edx.svg'
import CP from '../utils/img/cluth.svg'
import AWS from '../utils/img/awspartner.svg'

const Accomplishments = () => {
  return (
    <section className='mt-36 mb-24 text-[#223554]'>
        <div className="container-center">
            <h2 className='font-bold mb-8 w-full lg:w-1/2 mx-auto text-center'>We're proud of our accomplishments</h2>
            <p className='my-5 w-full lg:w-1/2 mx-auto text-center'>As a result of Arbisoft's experience and work ethic, we have formed valuable industry partnerships over the years. This has not only helped us in getting certified from industry leaders, but also in acquiring new businesses and customers.</p>
        </div>
        <div className='container-center mt-20 flex justify-around items-center flex-wrap space-x-4 space-y-2'>
            <img src={MGP} className='py-8 px-2' alt="Accomplishments" />
            <img src={MC} className='py-8 px-2' alt="Accomplishments" />
            <img src={ZYTE} className='py-8 px-2' alt="Accomplishments" />
            <img src={EDX} className='py-8 px-2' alt="Accomplishments" />
            <img src={CP} className='py-8 px-2' alt="Accomplishments" />
            <img src={AWS} className='py-8 px-2' alt="Accomplishments" />
            <img src={CP} className='py-8 px-2' alt="Accomplishments" />
        </div>
    </section>
  )
}

export default Accomplishments