import React from 'react'
import decadePartnerImg from '../utils/img/DecadePartner.png'

const DecadesPartnership = () => {
  return (
    <section className='my-24 flex flex-col md:flex-row container-center'>
        <div className='w-4/5 md:w-1/2 mb-8 sm:mb-0'>
            <img className='w-[95%] ml-6' src={decadePartnerImg} alt="DecadePartner" />
        </div>
        <div className='w-4/5 md:w-2/6 ml-5 md:ml-12'>
            <h2 className='mb-10 font-bold text-slate-700'>Decades of Partnership</h2>
            <p className='mb-8 md:text-[18px]'>We are a full-scale software development firm that knows the value of lasting relationships and can combine focused detail-oriented action with long-term strategic thinking.</p>
            <button className='btn bg-bl text-white min-w-max sm:text-[18px] py-3 px-8 bg-[#0a76db]'>View our technical skill set</button>
        </div>
    </section>
  )
}

export default DecadesPartnership